module.exports = ":root{--ck-color-sidebar-item-background:#fff;--ck-color-sidebar-item-drop-shadow:0 1px 1px 1px #e6e6e6}.ck.ck-sidebar{position:relative}.ck.ck-sidebar_item{position:absolute;width:100%;transition:top .3s ease,box-shadow .3s ease;outline:0;border-radius:var(--ck-border-radius);background:var(--ck-color-sidebar-item-background);box-shadow:var(--ck-color-sidebar-item-drop-shadow)}.ck.ck-sidebar_item:not(.ck-sidebar_item--active):hover{box-shadow:var(--ck-drop-shadow)}.ck.ck-sidebar_item--active{box-shadow:0 3px 6px 1px #e6e6e6}.ck.ck-sidebar_item--active .ck-annotation__actions,.ck.ck-sidebar_item:hover .ck-annotation__actions{opacity:1}.ck.ck-sidebar_item--active .ck-suggestion--accept,.ck.ck-sidebar_item:hover .ck-suggestion--accept{color:var(--ck-color-button-save)}.ck.ck-sidebar_item--active .ck-suggestion--discard,.ck.ck-sidebar_item:hover .ck-suggestion--discard{color:var(--ck-color-button-cancel)}"